
body {
  color:black;
  background: #cbcacf;
  font-size:  14px;
  font-family: cursive;
  margin:20px 20px 10px;
}
 
@media(max-width:425px){
  body{
      font-size: 10px;
  }
}

a{
  color:rgb(31, 150, 140);
}

a:hover{
  color:tomato;
}

@media(max-width:320px){
  a{
      font-size: 18px;
  }
}

form{
  display: flex;
  flex-direction: row;
}

h1{
  margin-top:15px;
  margin-bottom:15px;
}

@media (max-width:768px) {
  h1 {
      margin: 0px;
      text-align: center;
  }
}

input{
  font-size: 2em;
  font-family: cursive;
  border-radius: 5px;
  margin-left: 10px;
  margin-right: 10px;
  border:2px solid grey;
  width:25% ;
}

button:hover{
  color:tomato
}



.button-inline {
    border-radius: 5px;
    border: 2px solid gray;
    background: transparent;
    padding: 0 5px;
    font-size: 2em;
    font-family: cursive;
    cursor: pointer;
}

.btn {
    font-size: inherit;
    margin-left: 10px;
}

.btn-bott {
    display: block;
    margin: 15px auto 0px;
    font-weight: bold;
}





.table {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
}

.table-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    padding: 0 15px;
    background: #ffffff;
    border: 1px solid rgb(44, 42, 42);
    border-radius: 5px;
}
.aut-btn{
    display: flex;
    flex-direction: row;
}
.space{
    white-space: nowrap;
}
@media (max-width:768px) {
    .table-row {
        display: flex;
        flex-direction: column;
    }
}
